
* {
	outline: 0;
}
html, body {
	height: 100%;
	margin: 0;
	padding: 0;
	font-family: Helvetica, Arial, sans-serif;
}
#app {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	min-height: 100%;
}
.content {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	background-image: url(/static/img/bg_panel-admin.d53796e.jpg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	padding-bottom: 50px;
}
.subtitle {
	padding-top: 40px;
	padding-left: 35px;
	font-weight: normal;
}
.thirdTitle {
	padding-bottom: 20px;
	font-weight: bold;
}
.adminInput {
	padding: 10px 20px;
	border: 1px solid transparent;
	border-radius: 5px;
}
.adminBtn {
	padding: 10px;
	border-radius: 5px;
	border: 0;
	cursor: pointer;
	-webkit-transition: all 0.24s;
	transition: all 0.24s;
	background-color: #00C9A8;
	color: #fff;
}
.adminBtn:hover {
	background-color: #0BAD93;
}
.adminBtn:active {
	opacity: 0.7;
}
.delBtn {
	background-color: #FD9156;
}
.delBtn:hover {
	background-color: #FF7922;
}
.delSvg {
	margin-top: 2px;
	fill: #FFF
}
.errorMessage {
	padding-left: 15px;
	color: firebrick;
}
/*Animations*/
.modal-enter-active, .modal-leave-active {
	-webkit-transition: 0.3s;
	transition: 0.3s;
}
.modal-enter, .modal-leave-to {
	opacity: 0;
}

header {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	background-color: #00C9A8;
}
.header__title {
	margin-top: 25px;
	margin-bottom: 25px;
	margin-left: 30px;
	color: #fff;
}
.header__link {
	display: block;
	margin-right: 30px;
	color: #EFF0E9;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	text-decoration: none;
	border-bottom: 1px solid #EFF0E9;
}
.header__link:hover {
	border-bottom-color: transparent;
}


.tabsList {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 0;
	padding: 0;
	background-color: #EFF0E9;
	list-style: none;
}
.tablist__item {
	border-right: 2px solid #fff;
}
.tablist__item:hover {
	background-color: lightgray;
}
.tablist__item--active {
	background-color: #fff;
	cursor: default;
}
.tablist__item--active:hover {
	background-color: #fff;
}
.tablist__link {
	display: block;
	padding: 20px 80px;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	cursor: pointer;
	text-transform: uppercase;
	color: gray;
	text-decoration: none;
}
.tablist__link--active {
	background-color: #FFF;
	color: #00C9A8;
}








































