	padding-top: 30px;
	padding-left: 35px;
	padding-bottom: 35px;
	border-bottom: 1px solid lightgray;
}
.skillsList:last-child {
	border-bottom: 0;
}
.skillsList__table {
	padding-left: 20px;
}
.addNew {
	display: -webkit-box;
	display: -ms-flexbox;
