.skillsItem {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding-bottom: 20px;
}
.skillsItem__name {
	width: 120px;
	padding-right: 20px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.skillsItem__input {
	width: 40px;
	padding: 10px;
	border: 0;
	border-radius: 5px;
	text-align: center;
}
.skillsItem__units {
	padding-left: 10px;
}
.skillsItem__btnWrap {
	padding-left: 28px;
}
.skillsItem__btn {
	padding: 10px;
	text-align: center;
	border: 0;
	border-radius: 5px;
	background-color: #FD9156;
	color: #fff;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	cursor: pointer;
}
.skillsItem__btn:hover {
	background-color: #FF7922;
}
.skillsItem__btn:active {
	opacity: 0.7;
}

.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	z-index: 10;
}
.modal__overlay {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(169, 169, 169, 0.7);
	z-index: 20;
}
.modal__window {
	position: relative;
	z-index: 50;
	margin: auto;
	padding: 30px;
	background-color: #fff;
	border-radius: 5px;
}
.modal__close {
	position: absolute;
	top: 0;
	right: 0;
	padding: 10px;
	border: 0;
	background-color: transparent;
	cursor: pointer;
}
.modal__closeIcon {
	fill: gray;
}
.modal__content {
	padding-bottom: 20px;
	text-align: center;
	font-weight: bold;
	color: gray;
}
.modal__btn {
	width: 90px;
}
.modal__btn--del {
	margin-right: 20px;
	background-color: #FD9156;
}
.modal__btn--del:hover {
	background-color: #FF7922;
}

