.file-upload {
position: relative;
width: 233px;
height: 20px;
padding: 8px 4px;
overflow: hidden;
background: gray;
border-radius: 3px;
color: #fff;
text-align: center;
-webkit-transition: all 0.3s;
transition: all 0.3s;
font-size: 14px;
}
.file-upload:hover {
	background: lightgray;
}
.file-upload input[type="file"]{
	display: none;
}
.file-upload label {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	cursor: pointer;
}
.file-upload span {
	line-height: 36px;
}
